.maindown{
	line-height: 60px;
	border-top: 2px solid #2b2e33;
	border-bottom: none;
	padding-bottom: 80px;
  width: 1100px;
  display: -ms-flexbox;
  display: flex;
  margin: 0 auto;
}
.maindown .boleft{
	float: left;
	margin-top: 50px;
	background-color: #2b2e33;
	width: 880px;
	color: #fff;
}
.maindown .boleft input{
	margin-top: 23px;
	float: left;
	margin-left: 20px;
	margin-right: 10px;
}
.maindown .boleft>a{
	float: left;
}
.maindown .boleft .allc{
	float: left;
	font-size: 14px;
	margin-right: 10px;
}
.maindown .boleft .dele{
	margin-top: 20px;
	line-height: 16px;
	display: block;
	margin-left: 5px;
	font-size: 14px;
	border-right: 1px solid #6f7277;
	color: #fff;
	text-decoration: none;
	width: 146px;
}
.maindown .boleft .state{
	margin-left: 20px;
}
.maindown .boleft .state span {
	color: #c7ae13;
}
.maindown .boleft .coun{
	float: right;
	margin-right: 20px;
	color: #fbd800;
	margin-left: 10px;
	font-size: 28px;
	line-height: 60px;
	width: 130px;
}
.maindown .boleft  div{
	float: right;
	line-height: 18px;
	padding-top: 14px;
	font-size: 13px;
}
.maindown .boleft  div span{
		margin-left: 31px;
}
.maindown .boleft  div p{
  margin: 0;
}
.maindown button{
	margin-top: 50px;
	float:right;
	line-height: 55px;
	width: 220px;
	background-color: #fdd900;
	border: none;
	font-size: 24px;
	outline:none;
}
.maindown button:hover{
	background-color: #2b2e33;
	color:#fdd803;
}



