.maintop{
    width: 1200px;
    background-color: #fdd900;
    position: relative;
}
/*.maintop>.topon{
  display: flex;
  flex-wrap: wrap;
}*/
.maintop>.topon>h1{
    font-size: 32px;
    padding-top: 8px;
    line-height: 92px;
    margin-left: 78px;
    font-weight: normal;
    margin-top: 0;
}

.maintop>.topon>img{
    float: left;
    padding-top: 24px;
    margin-left: 51px;
}
.maintop>.topdown{
    height: 54px;
    background-image: url('../images/2_03.jpg');
    background-repeat: no-repeat;
}
.maintop>.topon>h1>span{
    font-size: 26px;
}
.maintop>.topon>h1>span>b{
    color: #f20201;
}
.topon>ul{
    list-style: none;
    float: left;
    position: absolute;
    top:42px;
    left: 800px;
}
.topon>ul>li{
    font-size: 18px;
    float: left;
}
.topon>ul>li{
    color: black;
    display: block;
    line-height: 36px;
    border-bottom: 1px solid #000;
}
.topon>ul>li:first-child{
    font-weight: bold;
    border-bottom: 3px solid #000;
    line-height: 35px;
    font-size: 20px;
    color: black;
}
.topon>ul>li:nth-child(2){
    margin:0 20px;
}



